$font-vollkorn: "Vollkorn", "Helvetica Neue", sans-serif;
/* vollkorn-600italic - latin */
@font-face {
  font-family: 'Vollkorn';
  font-style: italic;
  font-weight: 600;
  src: url('../font/vollkorn-v10-latin-600italic.eot'); /* IE9 Compat Modes */
  src: local('Vollkorn SemiBold Italic'), local('Vollkorn-SemiBoldItalic'),
  url('../font/vollkorn-v10-latin-600italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../font/vollkorn-v10-latin-600italic.woff2') format('woff2'), /* Super Modern Browsers */
  url('../font/vollkorn-v10-latin-600italic.woff') format('woff'), /* Modern Browsers */
  url('../font/vollkorn-v10-latin-600italic.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../font/vollkorn-v10-latin-600italic.svg#Vollkorn') format('svg'); /* Legacy iOS */
}